
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import FileItem from 'c%/FileItem.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'


export default{
mixins: [ common, globals ],
props: {
cid: {
type: [ String,Number ],
default: 0
},
sid: {
type: [ String,Number ],
default: 0
},
index: {
type: [ String,Number ],
default: 0
}
},
components: {
FileItem,
Timeline,
TimelineItem
},
data () {
return {
data: []
}
},
methods: {
editInfo (item) {
this.$emit('on-edit',item)
},
delInfo (item) {
let $this = this
this.$vux.confirm.show({
title: '提示',
content: '确认删除吗',

onCancel () {

},
onConfirm () {
$this.$rest('service_tasks')
.delete(item.st_id)
.success(data=>{
$this.toast('删除成功','success')
$this.downCallback()
}).error(response=>{
$this.toast(response.body.message||'删除失败~','warn')
})
}
})
}
},
computed: {
data_uri () {
let params = this.cid>0?'cid='+this.cid:'sid='+this.sid,
uri = util.sprintf('service_tasks?bid=-1&%s',params)

return uri
},
dict_type () {
if(this.cid>0){
return 'class_service_type'
}else{
return 'student_service_type'
}
}
}
}



