
import moment from 'moment'
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import PopupPicker from 'c%/PopupPicker.vue'

const emptyObject = {
sid: 0,
cu_id: 0,
cid: 0,
object_type: 2,
st_did: 0,
int_day: '',
int_hour: '',
own_eid: 0,
remark: '',
status: 0		
}

export default{
mixins: [ common, modal ],
directives: {
TransferDom
},
props: {

},
components: {
Cell,
Popup,
Group,
XButton,
Datetime,
XTextarea,
TransferDom,
PopupPicker
},
data () {
return {
info: util.copy(emptyObject),
name: '',
now_time:'',
sid: 0,
cid: 0
}
},
watch: {
'modal$.show': function (val) {
if(this.modal$.action=='add'){
this.info = util.copy(emptyObject)
this.lazy_init_day()
}
}
},
mounted(){
},
methods: {
ok (complete) {
let [uri,method] = this.modal$.action=='add'?['service_tasks','post']:['service_tasks/'+this.info.st_id,'put'],
$this = this
if(this.modal$.action=='edit'&&complete==true){
this.info.status = 1
}else{
this.info.status = 0
}
this.info.cid = this.cid
this.info.sid = this.sid
this.info.bid = this.bid$[0]

if(this.modal$.action=='add'){
this.check()
.then(()=>{
post()
})
}else{
post()
}
function post () {
$this.$rest(uri)[method]($this.info)
.success(data=>{
$this.toast('操作成功！','success')
$this.$emit('on-success')
$this.close()
}).error(response=>{
$this.$Message.error(response.body.message||'添加失败~')
})
}
},
check () {
return new Promise((resolve,reject)=>{
if(!this.info.int_day){
this.toast('请选择完成时间','warn')
reject()
}
if(!this.info.st_did){
this.toast('请选择服务类型','warn')
reject()
}
resolve()
})
},
lazy_init_day(){
setTimeout(()=>{
this.now_time = moment().format('YYYY-MM-DD HH:mm')
this.info.int_day = util.format_int_day(this.now_time.substr(0,10))
this.info.int_hour = util.format_int_hour(this.now_time.substr(11,5))
},1000)
},
timeChange (val) {
if(val){
this.info.int_day = util.format_int_day(val.substr(0,10))
this.info.int_hour = util.format_int_hour(val.substr(11,5))
}
}
},
computed: {
hourList () {
let result = []
for(let i=7;i<24;i++){
result.push(i)
}
return result
},
minuteList () {
let result = []
for(let i=0;i<60;i++){
result.push(i)
}

return result
},
dict_service_type () {
if(this.cid){
return 'class_service_type'
}
return 'student_service_type'
},
service_type () {
let list = this.dicts(this.dict_service_type),
result = []


if(list&&list.length>0){
list.forEach(l=>{
let obj = {
value: l.title,
key: l.did
}
result.push(obj)
})
}

return result
}
}
}



