
import moment from 'moment'
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import SelectFile from 'c%/SelectFile.vue'
import PopupPicker from 'c%/PopupPicker.vue'

const emptyObject = {
sid: 0,
cu_id: 0,
cid: 0,
object_type: 2,
st_did: 0,
int_day: '',
int_hour: '',
is_push: true,
rel_id: 0,
content: '',
eid: 0,
student_nums: 0,
service_record_file: []
}

export default{
mixins: [ common, modal ],
directives: {
TransferDom
},
props: {

},
components: {
Cell,
Popup,
Group,
XButton,
Datetime,
SelectFile,
XTextarea,
TransferDom,
PopupPicker
},
data () {
return {
info: util.copy(emptyObject),
name: '',
now_time:'',
sid: 0,
cid: 0
}
},
watch: {
'modal$.show': function (val) {
this.info = util.copy(emptyObject)
this.lazy_init_day()
}
},
mounted(){
},
methods: {
ok () {
if(!this.info.int_day){
this.toast('请选择完成时间','warn')
return
}
if(!this.info.st_did){
this.toast('请选择服务类型','warn')
return
}

this.info.cid = this.cid
this.info.sid = this.sid
this.info.bid = this.bid$[0]
this.$rest('service_records').post(this.info)
.success(data=>{
this.toast('添加成功！','success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.$Message.error(response.body.message||'添加失败~')
})
},
lazy_init_day(){
setTimeout(()=>{
this.now_time = moment().format('YYYY-MM-DD HH:mm')
this.info.int_day = util.format_int_day(this.now_time.substr(0,10))
this.info.int_hour = util.format_int_hour(this.now_time.substr(11,5))
},1000)
},
timeChange (val) {
if(val){
this.info.int_day = util.format_int_day(val.substr(0,10))
this.info.int_hour = util.format_int_hour(val.substr(11,5))
}
}
},
computed: {
hourList () {
let result = []
for(let i=7;i<24;i++){
result.push(i)
}
return result
},
minuteList () {
let result = []
for(let i=0;i<60;i++){
result.push(i)
}

return result
},
dict_service_type () {
if(this.cid){
return 'class_service_type'
}
return 'student_service_type'
},
service_type () {
let list = this.dicts(this.dict_service_type),
result = []


if(list&&list.length>0){
list.forEach(l=>{
let obj = {
value: l.title,
key: l.did
}
result.push(obj)
})
}

return result
}
}
}



