
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import RecordList from './record-list.vue'
import TaskList from './task-list.vue'
import AddRecord from './add-record.vue'
import AddTask from './add-task.vue'
import Progress from 'c%/progress'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default{
mixins: [ common, globals ],
props: {
cid: {
type: [ String,Number ],
default: 0
},
sid: {
type: [ String,Number ],
default: 0
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
RecordList,
TaskList,
AddRecord,
AddTask,
Progress
},
data () {
return {
list: ['服务记录','服务安排'],
tab: 0,
data: {},
map_sex: {0:'未确定',1:'男',2:'女'},
map_student_type: {0:'体验学员 ',1:'正式学员 ',2:'vip学员'},
map_rel_text: {0:'未设置',1:'自己',2:'爸爸',3:'妈妈',4:'其他'}
}
},
mounted () {
this.getServiceName()
},
methods: {
getTeachPercent (row) {
return (row.attendance_times / row.arrange_times) * 100
},
editInfo (item) {
let now_time = this.$filter('int_date')(item.int_day)+' ' +this.$filter('int_hour')(item.int_hour)

this.$refs['task']
.set('name',this.name)
.set('now_time',now_time)
.set('info',item)
.show('','edit')
},
handleSuccess () {
let ref = 'recordList'
if(this.tab==1){
ref = 'taskList'
}

this.$refs[ref].downCallback()
},
getServiceName () {
let uri = ''
if(this.cid > 0){
uri = 'classes/' + this.cid
}else if(this.sid > 0){
uri = 'students/' + this.sid
}

this.$rest(uri).get()
.success(data=>{
this.data = data
}).error(response=>{
this.toast(response.body.message||'获取信息失败~','warn')
})

},
addService () {
let ref = 'record'
if(this.tab==1){
ref = 'task'
}

this.$refs[ref]
.set('name',this.name)
.set('cid',this.cid)
.set('sid',this.sid)
.show()
}
},
computed: {
name () {
let field = 'student_name'
if(this.cid > 0){
field = 'class_name'
}
return this.data[field]||''
},
swiperHeight () {
let height = window.innerHeight
return (height - this.headerHeight - 44) +'px'
}
}
}



